/* global window */

/**
 * @customfunction
 */
export async function answer(notes: string, question: string[][][]): Promise<string> {
  const fullQuestion = question.flat().flat().join("\n");
  return await window.responder
    .response(fullQuestion, notes)
    .catch((e) => Promise.reject(new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidReference, e)));
}

CustomFunctions.associate("ANSWER", answer);